<template>
  <v-container fluid>
    <v-card>
      <v-toolbar color="toolbarColor" dense dark>
        <v-toolbar-title>Orders</v-toolbar-title>
      </v-toolbar>
      <v-card-text>
        <v-row>
          <v-col cols="12">
            <v-alert
              border="top"
              colored-border
              type="info"
              elevation="2"
            >No Orders Yet</v-alert>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    <snackbar ref="snackbar"></snackbar>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["serverUrl"]),
  },
  data() {
    return {
      loader: false,
      dialog: false,
      packages: [],
      deleteItem: {},
    };
  },
  methods: {
    openModal(data) {
      this.deleteItem = Object.assign({}, data);
      this.dialog = true;
    },
    deletePackage() {
      const url = `/packages/${this.deleteItem._id}`;
      const self = this;
      this.loader = true;
      this.$store
        .dispatch("expressRemove", url)
        .then((res) => {
          if (res.state) {
            location.reload();
          } else {
            self.$refs.snackbar.show(res.msg, "red");
          }
        })
        .catch((err) => {
          this.$refs.snackbar.show(err, "red");
        });
    },
    getData() {
      const url = "/packages";
      const self = this;
      this.$store
        .dispatch("expressGet", url)
        .then((res) => {
          if (res.state) {
            self.packages = res.records;
          }
        })
        .catch((err) => {
          console.log(err, "error");
          this.$refs.snackbar.show(err, "red");
        });
    },
  },
  created() {
    this.getData();
  },
};
</script>

